.hustle-modal:not(.hustle-modal-image_only) {

	.hustle-modal-image {
		
		@media all and (-ms-high-contrast: none), (-ms-high-contrast: active) {
			width: auto;
			height: auto;
			max-width: 100%;
			max-height: 100%;
			top: 50%;
			left: 50%;
			transform: translate(-50%,-50%);
		}
	}
}